$main: #4CAACD;


.navbar{
            // box-shadow: 0 0.15rem 1.75rem 0 rgb(58 59 69 / 15%);
            background-color:   #FFFFFF;
            width:100%;
            // height:30px;
            box-sizing: border-box;
            display: flex;
            align-items: center;

            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

            .logo{
                margin:0 1%;
            }
            #toggle {
                display: block;
                width: 40px;
                height: 32px;
                position: absolute;
                top: 3px;
                left: 5px;
                cursor: pointer;
                opacity: 0;
                z-index: 110;
                -webkit-touch-callout: none;
                position:absolute;
            }

        

            span {
                display: block;
                width: 33px;
                height: 4px;
                margin-bottom: 5px;
                top:5px;
                left:15px;
                position: relative;
                // position: fixed;

                background: $main;
                border-radius: 3px;
                z-index: 109;
                -webkit-transform-origin: 4px 0;
                transform-origin: 4px 0;
                transition: background .5s cubic-bezier(.77,.2,.05,1),opacity .55s ease,-webkit-transform .5s cubic-bezier(.77,.2,.05,1);
                transition: transform .5s cubic-bezier(.77,.2,.05,1),background .5s cubic-bezier(.77,.2,.05,1),opacity .55s ease;
                transition: transform .5s cubic-bezier(.77,.2,.05,1),background .5s cubic-bezier(.77,.2,.05,1),opacity .55s ease,-webkit-transform .5s cubic-bezier(.77,.2,.05,1);
            }


            #div-span{
                display: inline-block;
                position: fixed;
                top:23px;
                z-index: 110;
                left:150px;

                // border: 1px solid green;
            


            


            input:checked~span:nth-child(2) {
                opacity: 1;
                // z-index: 4;
                -webkit-transform: rotate(
            45deg
            ) translate(-2px,-1px);
                transform: rotate(
            45deg
            ) translate(-2px,-1px);
                background: #FF783C;
            }



            input:checked~span:nth-child(3) {
                opacity: 0;
                -webkit-transform: rotate(
            0deg
            ) scale(.2);
                transform: rotate(
            0deg
            ) scale(.2);
            }

        

            input:checked~span:nth-child(4) {
                opacity: 1;
                // z-index: 4;
                -webkit-transform: rotate(
            -45deg
            ) translate(1px,1px);
                transform: rotate(
            -45deg
            ) translate(-1px,-3px);
                background: #FF783C;
            }

        } 

            .btn-log-reg {
                border:0;
                cursor: pointer;
                display: inline-block;
                padding: 5px 10px;
                border-radius: 5px;
                color: #fff;
                font-size: 16px;
                line-height: 26px;
                // letter-spacing: .15rem;
                transition: all .3s;
                position: relative;
                overflow: hidden;
                z-index: 1;
                &:after {
                  content: '';
                  position: absolute;
                  bottom: 0;
                  left: 0;
                  width: 100%;
                  height: 100%;
                  background-color: $main;
                  border-radius: 5px;
                  z-index: -2;
                }
                &:before {
                  content: '';
                  position: absolute;
                  bottom: 0;
                  left: 0;
                  width: 0%;
                  height: 100%;
                  background-color: darken($main, 15%);
                  transition: all .3s;
                  border-radius: 5px;
                  z-index: -1;
                }
                &:hover {
                  color: #fff;
                  &:before {
                    width: 100%;
                  }
                }

                svg{
                    font-size: 20px;
                }
              }
              



            
            .navbar #toggle{
                top:1px;
                left:5px;
            }
            
            
            .navbar span{
                top:5px;
                left:15px;
            }
            
            
            .right-navbar{
                padding-left: 100px;
                padding-right:1% ;
                margin-left: auto;
                display: block;
                font-family: 'EB Garamond',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;



                ul{
                    min-height: 42px;
                    list-style: none; 
                    margin: 0; 
                    padding:3px 0; 
                    display: flex; 
                    align-items: center;
                    justify-content: flex-end;

                   }    
                
                li {
                   margin:0 15px;

                   .user-nav-header{
                    font-size: 20px;

                    svg{
                        font-size: 30px;
                    }
                   }

                   
                    }
            
            }  


}




